 96 / 128\r\n*/\r\n\r\n// Variables\r\n@use 'utils/variables';\r\n\r\n// Default styles\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n\r\n    outline: 3px solid transparent;\r\n}\r\n\r\n*:focus {\r\n    outline-color: variables.$darkest-primary-color;\r\n}\r\n\r\nhtml {\r\n    /* 1rem = 10px */\r\n    font-size: 62.5%;\r\n}\r\n\r\nbody {\r\n    font-family: 'Roboto', sans-serif;\r\n    color: variables.$primary-text-color;\r\n}\r\n\r\ninput[type='text'],\r\ninput[type='date'],\r\ninput[type='time'],\r\nselect {\r\n    padding: 0.8rem 1.2rem;\r\n    font-size: 1.2rem;\r\n    letter-spacing: 0.5px;\r\n    border: 1px solid variables.$divider-color;\r\n    border-radius: 3px;\r\n    transition: all 0.3s;\r\n}\r\n\r\ninput[type='date'],\r\ninput[type='time'] {\r\n    padding: 0.4rem;\r\n}\r\n\r\nselect {\r\n    cursor: pointer;\r\n    background-color: variables.$light-text;\r\n}\r\n\r\n// General classes\r\n.hidden {\r\n    display: none;\r\n}\r\n\r\n.button {\r\n    &,\r\n    &:link,\r\n    &:visited {\r\n        padding: 0.8rem 1.6rem;\r\n        border: none;\r\n        border-radius: 3px;\r\n        cursor: pointer;\r\n        background-color: transparent;\r\n        transition: all 0.3s ease-in-out;\r\n\r\n        display: inline-flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        gap: 0.8rem;\r\n    }\r\n\r\n    &:hover,\r\n    &:link:hover,\r\n    &:visited:hover,\r\n    &:active,\r\n    &:link:active,\r\n    &:visited:active {\r\n        opacity: 0.9;\r\n        box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);\r\n    }\r\n\r\n    &--primary,\r\n    &--primary:link,\r\n    &--primary:visited {\r\n        background-color: variables.$primary-color;\r\n        color: variables.$light-text;\r\n    }\r\n\r\n    &--secondary,\r\n    &--secondary:link,\r\n    &--secondary:visited {\r\n        background-color: variables.$secondary-color;\r\n        color: variables.$light-text;\r\n    }\r\n}\r\n\r\n@import 'style.scss';\r\n@import 'responsive.scss';\r\n","// Colors: https://www.materialpalette.com/ -> choose blue and red\r\n\r\n$primary-color: #004177;\r\n$dark-primary-color: #002e53;\r\n$darkest-primary-color: #002544;\r\n$secondary-color: #ef5b0c;\r\n$primary-text-color: #212121;\r\n$secondary-text-color: #686767;\r\n$light-text: #fff;\r\n$divider-color: #bdbdbd;\r\n$light-grey: #f6f6f6;\r\n$grey-color: #d2d2d2;\r\n$calendar-cells-border: 1px solid rgba($divider-color, 0.3);\r\n","@keyframes fade {\r\n    from {\r\n        opacity: 0;\r\n    }\r\n\r\n    to {\r\n        opacity: 1;\r\n    }\r\n}\r\n\r\n@keyframes fade-right {\r\n    from {\r\n        transform: translateX(-5%);\r\n        opacity: 0.5;\r\n    }\r\n\r\n    to {\r\n        transform: translateX(0);\r\n        opacity: 1;\r\n    }\r\n}\r\n","@use 'utils/variables';\r\n@use 'utils/mixins';\r\n@use 'utils/animations';\r\n\r\n// Navigation\r\nnav {\r\n    padding: 1.6rem 3.2rem;\r\n    font-size: 1.6rem;\r\n    border-bottom: 1px solid rgba(variables.$divider-color, 0.6);\r\n\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 3.2rem;\r\n\r\n    .nav__logo {\r\n        display: flex;\r\n        align-items: center;\r\n\r\n        &_image {\r\n            color: variables.$primary-color;\r\n            font-size: 2.4rem;\r\n        }\r\n\r\n        &__text {\r\n            margin-left: 1.2rem;\r\n        }\r\n    }\r\n\r\n    .nav__movement .button {\r\n        @include mixins.padding-horizontal(0.8rem);\r\n    }\r\n}\r\n\r\n// Calendar table\r\n.calendar-table {\r\n    font-size: 1.2rem;\r\n    position: relative;\r\n\r\n    &.week .calendar__header,\r\n    &.week .calendar__body {\r\n        grid-template-columns: 4rem repeat(7, 1fr);\r\n    }\r\n\r\n    &.four .calendar__header,\r\n    &.four .calendar__body {\r\n        grid-template-columns: 4rem repeat(4, 1fr);\r\n    }\r\n\r\n    &.day .calendar__header,\r\n    &.day .calendar__body {\r\n        grid-template-columns: 4rem 1fr;\r\n    }\r\n\r\n    &.day .event-editor {\r\n        animation: fade 0.3s;\r\n        left: 50% !important;\r\n        transform: translateX(-50%);\r\n    }\r\n\r\n    .calendar__header,\r\n    .calendar__body {\r\n        display: grid;\r\n        align-items: stretch;\r\n    }\r\n\r\n    // Calendar header\r\n    .calendar__header {\r\n        border-bottom: variables.$calendar-cells-border;\r\n        padding: 1.2rem 2.4rem;\r\n        padding-right: 0;\r\n\r\n        position: sticky;\r\n        top: 0;\r\n        background-color: variables.$light-text;\r\n        z-index: 9;\r\n\r\n        div {\r\n            @include mixins.center-flex;\r\n            flex-direction: column;\r\n            gap: 0.4rem;\r\n        }\r\n\r\n        div:not(:last-child) {\r\n            border-right: variables.$calendar-cells-border;\r\n        }\r\n    }\r\n\r\n    // Calendar body\r\n    .calendar__body {\r\n        padding-left: 2.4rem;\r\n        position: relative;\r\n        overflow: hidden;\r\n\r\n        .calendar__body__row {\r\n            display: flex;\r\n            flex-direction: column;\r\n\r\n            &:nth-child(even) {\r\n                background-color: rgba(variables.$divider-color, 0.05);\r\n            }\r\n\r\n            .calendar__hour {\r\n                padding-right: 0.8rem;\r\n                @include mixins.center-flex;\r\n            }\r\n        }\r\n\r\n        .calendar__cell {\r\n            border-left: variables.$calendar-cells-border;\r\n            border-bottom: variables.$calendar-cells-border;\r\n            cursor: pointer;\r\n            position: relative;\r\n        }\r\n    }\r\n}\r\n\r\n//  Event editor\r\n.event-editor {\r\n    position: absolute;\r\n    background-color: variables.$light-text;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: 10;\r\n\r\n    box-shadow: 0 0 18px rgba(variables.$primary-text-color, 0.2);\r\n    width: 35rem;\r\n    animation: fade-right 0.35s ease-out;\r\n\r\n    .event-header {\r\n        display: flex;\r\n        justify-content: space-between;\r\n\r\n        font-size: 1.6rem;\r\n        padding: 1.2rem 1.6rem;\r\n        background-color: variables.$dark-primary-color;\r\n        color: variables.$light-text;\r\n\r\n        .exit-button {\r\n            background-color: transparent;\r\n            color: inherit;\r\n            border: none;\r\n            transition: opacity 0.3s ease-in;\r\n            cursor: pointer;\r\n\r\n            &:hover {\r\n                opacity: 0.7;\r\n            }\r\n        }\r\n    }\r\n\r\n    .event-form__container {\r\n        padding: 1.6rem;\r\n    }\r\n\r\n    .time {\r\n        display: flex;\r\n        flex-direction: column;\r\n        gap: 1.2rem;\r\n\r\n        & > div {\r\n            display: grid;\r\n            grid-template-columns: 1.2fr 5fr 5fr;\r\n            column-gap: 1rem;\r\n            align-items: center;\r\n\r\n            label {\r\n                display: grid;\r\n                grid-template-columns: 1fr 9fr;\r\n                column-gap: 0.4rem;\r\n                align-items: center;\r\n            }\r\n\r\n            svg {\r\n                color: variables.$secondary-text-color;\r\n                margin-right: 0.4rem;\r\n            }\r\n        }\r\n    }\r\n\r\n    form {\r\n        display: flex;\r\n        flex-direction: column;\r\n        gap: 1.2rem;\r\n    }\r\n\r\n    .buttons {\r\n        display: grid;\r\n        grid-template-columns: 1fr 1fr;\r\n        column-gap: 1rem;\r\n    }\r\n}\r\n\r\n// Events\r\n.event {\r\n    position: relative;\r\n    z-index: 1;\r\n\r\n    border-radius: 3px;\r\n    padding: 3px 6px;\r\n    position: absolute;\r\n    left: 6px;\r\n    right: 6px;\r\n    background-color: variables.$primary-color;\r\n    color: variables.$light-text;\r\n    touch-action: none;\r\n    box-shadow: 0 0 5px rgba(0, 0, 0, 0.6);\r\n\r\n    &.event-hover,\r\n    &:hover,\r\n    &:active {\r\n        box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);\r\n    }\r\n\r\n    .event-container {\r\n        height: 100%;\r\n        overflow: hidden;\r\n    }\r\n\r\n    .title,\r\n    .description {\r\n        width: 100%;\r\n        overflow: hidden;\r\n        text-overflow: ellipsis;\r\n        font-size: 1.2rem;\r\n    }\r\n\r\n    .description {\r\n        color: variables.$grey-color;\r\n    }\r\n\r\n    .time {\r\n        margin-top: 0.8rem;\r\n        font-size: 1rem;\r\n    }\r\n\r\n    .resize-box {\r\n        position: absolute;\r\n        height: 15px;\r\n        bottom: 0;\r\n        transform: translateY(50%);\r\n        left: 0;\r\n        right: 0;\r\n        z-index: 2;\r\n        cursor: s-resize;\r\n        touch-action: none;\r\n    }\r\n}\r\n","@mixin padding-horizontal($left, $right: $left) {\r\n    padding-left: $left;\r\n    padding-right: $right;\r\n}\r\n\r\n@mixin center-flex {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n","@media (max-width: 820px) {\r\n    html {\r\n        /* 8px */\r\n        font-size: 50%;\r\n    }\r\n}\r\n\r\n@media (max-width: 470px) {\r\n    nav {\r\n        display: grid;\r\n        grid-template-columns: repeat(3, 1fr);\r\n        justify-items: center;\r\n    }\r\n\r\n    .event-editor {\r\n        left: 50% !important;\r\n        transform: translate(-50%, -125%) !important;\r\n        width: 90%;\r\n    }\r\n}\r\n\r\n@media (max-width: 340px) {\r\n    nav {\r\n        grid-template-columns: repeat(2, 1fr);\r\n    }\r\n}\r\n\r\n@media (max-width: 250px) {\r\n    nav {\r\n        grid-template-columns: 1fr;\r\n    }\r\n}\r\n"],"sourceRoot":""}]);
